.home-slide :global .slick-slide {
  text-align: center;
  height: 4.26666667rem;
  line-height: 4.26666667rem;
  background: #364d79;
  overflow: hidden;
  color: #fff;
}

.home-slide :global .slick-slide img {
  width: 100%;
  height: 4.26666667rem;
}

.home-slide :global .slick-dots {
  bottom: 0.33333333rem;
}

.main-content {
  position: fixed;
  top: 1.33333333rem;
  left: 0;
  width: 100%;
  height: calc(100vh - 2.93333333rem);
  overflow-y: auto;
}