.home-header {
  padding-right: 0.26666667rem;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100vw;
  height: 1.33333333rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  background-color: #0088f9;
}

.logo {
  height: 1.33333333rem;
}

.avatar {
  height: 1.33333333rem;
  width: 1.33333333rem;
  border-radius: 50%;
}

.menu {
  position: absolute;
  left: 0;
  top: 1.33333333rem;
  width: 100vw;
  border-top: 0.01333333rem solid #f9fcff;
}

.item {
  padding: 0.8em 0;
  width: 100%;
  font-size: 0.42666667rem;
  text-align: center;
  border-bottom: 0.01333333rem solid #f9fcff;
  background-color: #0088f9;
}

.active {
  color: #000;
}